.header {
  height: $header-height;
  padding-left: map-get($font-size, h1) * $cap-icon-width;
  display: flex;
  align-items: center;
}

.header__headline,
.header__subhead {
  line-height: 1;
  margin: 0;
}

.header__headline {
  font-size: map-get($font-size, jumbo);
  letter-spacing: em(-0.45pt);
}

.header__subhead {
  text-transform: uppercase;
  letter-spacing: em(-0.35pt);
}

.header__accent {
  font-weight: 400;
}
