$base-font-size: 16pt !default;

@function em($pt, $ctx: $base-font-size) {
  @if (unitless($pt)) {
    $pt: $pt * 1pt;
  }

  @if (unitless($ctx)) {
    $ctx: $ctx * 1pt;
  }

  @return $pt / $ctx * 1em;
}

@function rem($pt, $ctx: $base-font-size) {
  @if (unitless($pt)) {
    $pt: $pt * 1pt;
  }

  @if (unitless($ctx)) {
    $ctx: $ctx * 1pt;
  }

  @return $pt / $ctx * 1rem;
}

@function pow($number, $exponent) {
  $value: 1;

  @if $exponent > 0 {
    @for $i from 1 through $exponent {
      $value: $value * $number;
    }
  } @else if $exponent < 0 {
    @for $i from 1 through -$exponent {
      $value: $value / $number;
    }
  }

  @return $value;
}
