// Sizes
// ===================================================
$phi:                          1.618033988749895;
$golden:                       $phi;

// ## Page
$page-width:                   8.5in;
$page-height:                  11in;

// ## Main
$main-width:                   $page-width / $golden;
$main-spacing:                 0.5in;

// ## Header
$header-height:                ($page-width - $main-width) / $golden;

// ## Sidebar
$sidebar-width:                $page-width - $main-width;
$sidebar-horizontal-spacing:   1in / pow($golden, 2);
$sidebar-top-spacing:          $header-height;
$sidebar-padding:              $sidebar-top-spacing $sidebar-horizontal-spacing $sidebar-horizontal-spacing;

// ## Icon
$cap-icon-width:               1.2861328125;


// Colors
// ===================================================

$base-color:                   #806147;
$white:                        #fff;
$black:                        #000;

$gray: (
  50:                          #fafaf9,
  100:                         #f5f4f4,
  200:                         #eeedec,
  300:                         #e0dedc,
  400:                         #bdb9b6,
  500:                         #9e9893,
  600:                         #756e68,
  700:                         #615b56,
  800:                         #423e3b,
  900:                         #211f1d,
);

$text-color:                   map-get($gray, 900);
$body-bg:                      $white;


// Typography
// ===================================================

$global-font-size:             8.25pt;

$base-font-family:             "Open Sans", "sans-serif";
$base-font-size:               $global-font-size;
$base-font-weight:             300;
$base-line-height:             1.6;

$heading-font-family:          "Source Sans Pro", "sans-serif";
$heading-line-height:          1.4;
$heading-font-weight:          300;

$bold-font-weight:             600;

$large-font-size:              10pt;
$small-font-size:              em($base-font-size * 0.875);
$lead-font-size:               em($base-font-size * 1.25);

$font-size: (
  jumbo:                       em(round($base-font-size * 4.236)),
  h1:                          em(round($base-font-size * 3.236)),
  h2:                          em(round($base-font-size * 2.618)),
  h3:                          em(round($base-font-size * 2)),
  h4:                          em(round($base-font-size * 1.618)),
  h5:                          em(round($base-font-size * 1.236)),
  h6:                          em($base-font-size),
);
