.article {
  margin-bottom: em(15pt);
  position: relative;

  &:last-of-type:not(:only-of-type),
  &--end {
    margin-bottom: 0;
  }

  &--decor {
    padding-left: map-get($font-size, h1) * $cap-icon-width;

    &::before {
      content: "";
      position: absolute;
      left: map-get($font-size, h1) / $cap-icon-width / 2;
      height: 100%;
      width: 1px;
      background-color: map-get($gray, 400);
    }
  }
}

.article__header {
  margin-bottom: em(5pt);
}

.article__headline {
  font-weight: 600;
  position: relative;
}

.article__subhead {
  font-weight: 400;
}

.article__lead {
  font-size: map-get($font-size, h5);
}

.article__list {
  margin-left: em(11.25pt);
}

.article__list-item {
  &:last-child {
    margin-bottom: 0;
  }
}

.article__list-icon {
  margin-left: em(-11.25pt);

  &--mright {
    margin-right: em(5pt);
  }
}

.article__link {
  vertical-align: middle;
}
