.block {
  margin-bottom: 1in / pow($golden, 2);

  &:last-child {
    margin-bottom: 0;
  }
}

.block__title {
  color: $text-color;
  margin-bottom: em(3pt);
  text-transform: uppercase;
}

.block__icon {
  color: $text-color;
  margin-right: ($cap-icon-width - 1) * 1em;

  &--cap {
    margin-right: 0;
  }
}
